import { Box, Typography, Link } from '@mui/material';


export default function Links() {

    return (
        <Box
            className="d-flex justify-content-center m-auto p-auto"
        >
            <Typography
                variant="h4"
                sx={{
                    fontFamily: "monospace",
                }}
            >
                <Link
                    href="https://github.com/besigne/"
                    underline="none"
                    color="black"
                    sx={{
                        margin: 1
                    }}
                >
                    Github
                </Link>
                <Link
                    href="https://www.linkedin.com/in/matheus-boesing-285031163/"
                    underline="none"
                    color="black"
                    sx={{
                        margin: 1
                    }}
                >
                    Linkedin
                </Link>
            </Typography>
        </Box>
    )
}