import { Box } from '@mui/material';
import PerfilImage from '../imgs/perfil.jpeg';

export default function Perfil() {
    return (

        <Box
            className="d-flex flex-row justify-content-center m-auto p-auto"
        >
            <Box
                component="img"
                src={PerfilImage}
                alt="me"
                sx={{
                    width: 350,
                    height: 450,
                    border: 3,
                    borderColor: 'black',
                    borderRadius: 50
                }}
                className="m-3"
            />
        </Box>

    )
}